.box{
    border-radius: 7px;
    border: 1px solid rgba(255, 255, 255, 0.048);
    box-sizing: border-box;
}

.box:hover{
    border: 1px dashed rgba(255, 255, 255, 0.24);
    transition: ease-in-out;
}

.content{  
    border-radius: 7px;
    width: 100%;
    height: 100%;
    transition: 0.4s ease-in-out;
    text-transform: capitalize;
    letter-spacing: 0.5px;
    padding: 1em;
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-gap: 4px;
    gap: 4px;
    position: relative;
    font-size: 0.9em;
}

.content:hover .circle, .content:hover .up-down span{
    color: white;
    border-color: white !important; 
}

.figure{
    font-size: 1.5em;
}

.icon{
    justify-self: end;
}

.circle{
   width: 70px;
   height: 70px;
   border-radius: 50%;
   border: 5px solid rgb(136, 62, 165);
   padding: 1.3em 1em;
   grid-auto-rows: minmax(50px);
   margin-right: 20px;
}

.up-down{
    position: absolute;
