.ApplicantsList::-webkit-scrollbar {
    width: 2px;
  }
  
  /* Track */
  .ApplicantsList::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  .ApplicantsList::-webkit-scrollbar-thumb {
