:root{
    --first : rgb(134, 212, 226);
    --second : rgb(29, 105, 192);
    --third : rgb(201, 83, 112);
    --fourth : white;
    --fifth : rgb(159, 66, 212);
}

.measures{
    width: 30%;
    border: 1px solid rgba(255, 255, 255, 0.075);
    position: relative;
    left: -2em;
    border-radius: 5px;
    padding: 0.5em 1em;
}

.measurements li:first-child{
    color: rgb(134, 212, 226);
    color: var(--first);
}

.measurements li:nth-child(2){
    color: rgb(29, 105, 192);
    color: var(--second);
}

.measurements li:nth-child(3){
    color: rgb(201, 83, 112);
    color: var(--third);
}

.measurements li:nth-child(4){
    color: white;
    color: var(--fourth);
}

.measurements li:nth-child(5){
    color: rgb(159, 66, 212);
    color: var(--fifth);
}

.measurements {
    padding: 0.4em 0.4em;
}

.measurement{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 0.7em;
}

.span1{
    -webkit-align-self: center;
            align-self: center;
}

.measurement:nth-child(even) .span1{
   -webkit-filter: invert(1);
           filter: invert(1);
   
}
